import request from '@/utils/request'

export function getList(params) {
	return request({
		url: '/todo/list',
		method: 'get',
		params,
	})
}

export function getListCount(params) {
	return request({
		url: '/todo/todo_count',
		method: 'get',
		params,
	})
}

export function add(params) {
	return request({
		url: '/todo/add',
		method: 'post',
		data: params,
	})
}

export function update(params) {
	return request({
		url: '/todo/update',
		method: 'post',
		data: params,
	})
}

export function del(params) {
	return request({
		url: '/todo/delete',
		method: 'post',
		data: params,
	})
}
